/* Main */
body {
    background: transparent url(./css/background.jpeg) no-repeat fixed center center/cover;
    margin: 0 auto;
    position: relative;
}
#header-title{
  /* color: linear-gradient(90deg,#ffd301 0%,#f4702a 21%,#ee254a 40%,#ed2870 60%,#c52378 80%,#861766 100%);
  font-size: 72px; */
  background: -webkit-linear-gradient(90deg,#ffd301 0%,#f4702a 21%,#ee254a 40%,#ed2870 60%,#c52378 80%,#861766 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

#main-navbar{
width: 100%;
}
