/* login */
.login-form-container{
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  width: 90%;
  background-color: #FFF;
  border-radius: 10px;
  box-shadow: 0 5px 8px 0 rgba(0, 0, 0, 0.2), 0 9px 26px 0 rgba(0, 0, 0, 0.19);
}

.login-form-div{
  display: flex;
  justify-content: center;
}

/* Homepage */

.homepage-container{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  align-content: center;
}

.homepage-link{
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;
}

.btn-circle.btn-xl {
  width: 70px;
  height: 70px;
  padding: 10px 16px;
  border-radius: 35px;
  font-size: 12px;
  text-align: center;
}

/* PROFILE */
.profile-form-div{
  display: flex;
  justify-content: center;
}

.profile-form-container{
  max-width: 450px;
}

h1{
  text-align: center;
  margin: 50px;
}

.ins-card{
  width: 220px;
}

input{
  border-radius: 10px;
  border: 1px solid #3679F6;
  box-shadow: 0 1px 1px 0;
}

input[type="text"]:disabled {
  border-radius: 10px;
  background: #FFF;

}

input[type="text"]:selection {
  border: 1px solid blue;
  border-radius: 10px;
}

/* RECORDS */
.record-form-div{
  display: flex;
  justify-content: center;
}

ul{
  list-style-type: none;
}

.li-record{
  border: solid black 1px;
  width: 300px;
  text-align: center;
  margin: 10px;
}

.appt-img{
  width: 220px;
}

.appt-filter-container{
  display: flex;
  justify-content: center;
  flex-wrap: wrap;

}

.appt-category-button{
  margin: 2px;

}

/* Add Record- modal */
.addrecord-button-container{
  display: flex;
  justify-content: flex-end;
  margin: 20px;
}

/* Find Care */
.map{
  max-width: 200px;
  max-height: 200px;
}


.findcare-container{
  display: flex;
}

.findcare-locationsList-container{
  display: flex;

}
